import { IAmenitiesInteractiveList } from "src/types";

const amenities: IAmenitiesInteractiveList[] = [
  {
    name: "ATM",
    type: "atm",
  },
  {
    name: "Management",
    type: "management",
  },
  {
    name: "Kids Area",
    type: "playarea",
  },
  {
    name: "Restroom",
    type: "restroom",
  },
  {
    name: "Family Restroom",
    type: "family-restroom",
  },
  {
    name: "Elevator",
    type: "elevator",
  },
  {
    name: "Security",
    type: "security",
  },
];

export const getAllAmenities = () => {
  const amenitiesWithSvg = amenities.map((item) => {
    let svg;
    const svg_block = document.getElementById(`map-special-svg-${item.type}`);
    if (svg_block) {
      const svg_element = svg_block.querySelector('svg');
      if (svg_element) {
        const svgColoredParts = svg_element.querySelectorAll('[fill]');
        svgColoredParts.forEach((svgColoredPart) => {
          svgColoredPart.setAttribute('fill', '#FFFFFF');
        });
        svg = (new XMLSerializer()).serializeToString(svg_element);
      }
    }
    return {
      ...item,
      svg
    };
  });
  return amenitiesWithSvg;
}